.customizer-wrap{position:fixed;right:-232px;top:100px;z-index:99999;background-color:#fff;width:232px;transition:all .3s ease 0s}.customizer-wrap.open{right:0}.customizer-toggle{position:absolute;background-color:#fff;color:#333;left:-39px;width:40px;height:40px;text-align:center;border:1px solid #eee}.customizer-toggle i{font-size:14px;line-height:40px}.customizer-inner{padding:20px;border:1px solid #eee;position:relative}.customizer-inner h4{color:#333;font-size:16px;line-height:1;margin-bottom:10px}.color-setting{display:flex;margin:0 -5px;flex-wrap:wrap}.color-setting li{border:1px solid transparent;border-radius:50%;width:30px;height:30px;display:block;cursor:pointer;margin:0 5px}.color-setting li.active{border-color:#efefef}.theme-setting{display:flex;margin:0 -5px;flex-wrap:wrap}.theme-setting li{border:1px solid #eee;width:30px;height:30px;display:block;cursor:pointer;margin:0 5px}.dark-version .customizer-wrap{background-color:#333}.dark-version .customizer-toggle{background-color:#333;color:#fff;border:1px solid #4d4d4d}.dark-version .customizer-inner{border:1px solid #4d4d4d}.dark-version .customizer-inner h4{color:#fff}.dark-version .color-setting li.active{border-color:#ccc}.dark-version .theme-setting li{border:1px solid #4d4d4d}.btn-theme{border:none;width:100px;border-radius:100px;line-height:0;height:40px;background-color:#e6e6e6;box-shadow:rgba(0,0,0,.1) 0 2px 10px;position:relative}.btn-theme:after{content:"Dark";margin-left:28px;font-weight:600;text-transform:uppercase}.btn-theme:before{content:"Light";margin-left:-30px;font-weight:600;text-transform:uppercase;display:none}.btn-theme .ball{height:30px;width:30px;background:#fff;position:absolute;border-radius:50%;box-shadow:rgba(0,0,0,.1) 0 2px 10px;top:50%;left:5px;transform:translateY(-50%);transition:left .4s;pointer-events:none}.btn-theme .ball:after{font-size:20px;font-family:"FontAwesome";position:absolute;top:50%;transform:translate(-50%);left:50%;pointer-events:none}.btn-theme.active{background-color:#444}.btn-theme.active:after{display:none}.btn-theme.active:before{display:inline-block}.btn-theme.active .ball{background-color:#292929;left:64px}.btn-theme.active .ball:after{content:"";font-size:16px}